/* Profile.css */
.profile-container {
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
  padding: 20px;
}

.sidebar {
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: fixed;
  left: 20px;
  top: 100px;
}

.profile-header {
  display: flex;
  align-items: center;
  justify-content: center; /* Í∞ÄÏö¥Îç∞ Ï†ïÎ†¨ */
  gap: 20px; /* ÌîÑÎ°úÌïÑ ÏÇ¨ÏßÑÍ≥º ÎãâÎÑ§ÏûÑ, ÌåîÎ°úÏö∞ Î≤ÑÌäº ÏÇ¨Ïù¥ Í∞ÑÍ≤© */
  margin-bottom: 20px;
}

.profile-picture {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin-bottom: 10px;
  object-fit: cover;
}

.profile-info {
  text-align: center; /* Í∞ÄÏö¥Îç∞ Ï†ïÎ†¨ */
  display: flex;
  flex-direction: column; /* ÎãâÎÑ§ÏûÑÍ≥º ÌåîÎ°úÏö∞ Î≤ÑÌäºÏùÑ ÏÑ∏Î°úÎ°ú Î∞∞Ïπò */
  gap: 10px; /* ÎãâÎÑ§ÏûÑÍ≥º ÌåîÎ°úÏö∞ Î≤ÑÌäº ÏÇ¨Ïù¥Ïùò Í∞ÑÍ≤© */
}

.nickname {
  font-size: 20px;
  font-weight: bold;
}

.follow-button {
  margin-top: 10px;
  padding: 8px 16px;
  font-size: 14px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.follow-button:hover {
  background-color: #0056b3;
}

.follow-info {
  margin-top: 15px;
  display: flex;
  justify-content: center; /* ÌåîÎ°úÏõå/ÌåîÎ°úÏûâ Ï†ïÎ≥¥ Í∞ÄÏö¥Îç∞ Ï†ïÎ†¨ */
  gap: 30px; /* ÌåîÎ°úÏõå, ÌåîÎ°úÏûâ ÏÇ¨Ïù¥ Í∞ÑÍ≤© */
  font-size: 14px;
}

.follow-info span {
  cursor: pointer;
  color: #007bff;
  font-weight: bold;
}

.follow-info span:hover {
  text-decoration: underline;
}

.post-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5px;
  margin-top: 20px;
}

.post-item {
  width: 100%;
  padding-top: 100%;
  position: relative;
  background-color: #f0f0f0;
  cursor: pointer;
}

.post-thumbnail {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.follow-info button {
  background-color: #007bff;  /* Î∞∞Í≤ΩÏÉâ */
  color: white;  /* Í∏ÄÏûê ÏÉâ */
  border: none;  /* ÌÖåÎëêÎ¶¨ Ï†úÍ±∞ */
  padding: 1px 12px;  /* ÎÇ¥Î∂Ä Ïó¨Î∞± Ï°∞Ï†ï (ÌÅ¨Í∏∞ Ï∂ïÏÜå) */
  border-radius: 5px;  /* Îë•Í∑º Î™®ÏÑúÎ¶¨ */
