  margin: 0 5px;  /* Î≤ÑÌäº ÏÇ¨Ïù¥ Ïó¨Î∞±ÏùÑ Ï¢Ä Îçî Ï¢ÅÌûàÍ∏∞ */
  text-align: center;
  display: inline-block;  /* Î≤ÑÌäºÎì§ÏùÑ Ïù∏ÎùºÏù∏ Î∏îÎ°ùÏúºÎ°ú Î∞∞Ïπò */
  width: auto;  /* Î≤ÑÌäºÏùò ÎÑàÎπÑ ÏûêÎèô Ï°∞Ï†ï */
}

