.post-form-container {
  width: 600px; /* Î©îÏù∏ ÌôîÎ©¥Î≥¥Îã§ ÏÇ¥Ïßù ÎÑìÍ≤å */
  margin: 50px auto;
  padding: 20px;
  background: white;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* üìå Í≤åÏãúÍ∏Ä ÏûÖÎ†•Ïπ∏ ÌÅ¨Í∏∞ & ÎÑàÎπÑ ÏµúÏ†ÅÌôî */
.post-content-input {
  width: calc(100% - 20px); /* Î∂ÄÎ™® ÏöîÏÜå Î≤óÏñ¥ÎÇòÏßÄ ÏïäÎèÑÎ°ù Ï°∞Ï†ï */
  height: 120px; /* Ï†ÅÎãπÌïú ÎÜíÏù¥ Ïú†ÏßÄ */
  padding: 12px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  resize: none; /* ÌÅ¨Í∏∞ Ï°∞Ï†à Î∞©ÏßÄ */
  box-sizing: border-box; /* ÎÇ¥Î∂Ä Ìå®Îî© Ìè¨Ìï®ÌïòÏó¨ ÌÅ¨Í∏∞ Ï°∞Ï†à */
}

/* üìå ÌÉúÍ∑∏ ÏûÖÎ†•Ïπ∏ & Î≤ÑÌäº Ï†ïÎ†¨ */
.tag-input-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  margin-top: 10px;
}

.tag-input-container input {
  width: calc(100% - 20px); /* Î≤ÑÌäºÍ≥º Í∞ÑÍ≤© ÎßûÏ∂îÍ∏∞ */
  padding: 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box; /* ÌÅ¨Í∏∞ Ï°∞Ï†à Ïò§Î•ò Î∞©ÏßÄ */
}

.tag-input-container button {
  width: 100px;
  margin-top: 6px;
  padding: 8px;
  font-size: 14px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.tag-input-container button:hover {
  background-color: #0056b3;
}

/* üìå ÌÉúÍ∑∏ Î™©Î°ù Ïä§ÌÉÄÏùº */
.tag-list {
  display: flex;
  flex-wrap: wrap; /* ÌÉúÍ∑∏Í∞Ä ÎßéÏïÑÏßÄÎ©¥ ÏûêÎèô Ï§Ñ Î∞îÍøà */
  gap: 8px; /* ÌÉúÍ∑∏ Í∞ÑÍ≤© */
  margin-top: 8px;
  align-items: center; /* ÌÉúÍ∑∏ ÎÜíÏù¥ Í∑†ÏùºÌïòÍ≤å ÎßûÏ∂îÍ∏∞ */
}

.tag {
  display: flex;
  align-items: center;
  background-color: #f0f0f0;
  padding: 6px 10px;
  border-radius: 16px;
  font-size: 14px;
  color: #333;
  white-space: nowrap; /* Îã®Ïñ¥Í∞Ä ÏûòÎ¶¨ÏßÄ ÏïäÍ≤å */
  max-width: 200px; /* ÌÉúÍ∑∏Í∞Ä ÎÑàÎ¨¥ Í∏∏Ïñ¥ÏßÄÏßÄ ÏïäÎèÑÎ°ù ÏÑ§Ï†ï */
  overflow: hidden;
  text-overflow: ellipsis; /* ÎÑàÎ¨¥ Í∏∏Î©¥ "..." ÌëúÏãú */
}

.tag button {
  background: none;
  border: none;
  margin-left: 5px;
  cursor: pointer;
  font-weight: bold;
  color: red;
}

/* üìå Í≤åÏãú Î≤ÑÌäº */
.post-submit-button {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 15px;
}

.post-submit-button:hover {
  background-color: #218838;
}

.image-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.image-filename {
  font-size: 12px;
  color: #666;
  margin-top: 4px;
  max-width: 100px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
