/* Reply.module.css */

.overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5); /* Î∞∞Í≤ΩÏùÑ Î∞òÌà¨Î™ÖÌïòÍ≤å */
  z-index: 1000;
}

.replyContainer {
  display: flex;
  background-color: white;
  width: 80%;
  max-width: 1200px;
  height: 80%;
  border-radius: 10px;
}

.left {
  flex: 1;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.postImage {
  width: 100%; /* Ïù¥ÎØ∏ÏßÄÏùò Í∞ÄÎ°úÎ•º Ïª®ÌÖåÏù¥ÎÑà ÌÅ¨Í∏∞Ïóê ÎßûÍ≤å */
  height: 600px; /* Ïù¥ÎØ∏ÏßÄÏùò ÎÜíÏù¥Îäî ÏûêÎèôÏúºÎ°ú ÎπÑÏú®Ïóê ÎßûÍ≤å */
  object-fit: contain; /* Ïù¥ÎØ∏ÏßÄÍ∞Ä ÏûòÎ¶¨ÏßÄ ÏïäÎèÑÎ°ù ÎπÑÏú®ÏùÑ Ïú†ÏßÄÌïòÎ©∞ Ï†ÑÏ≤¥Í∞Ä ÎÇòÏò§Í≤å */
  border-radius: 10px;
}

.postContent {
  margin-top: 10px;
  text-align: center;
}

.nickname {
  font-weight: bold;
  font-size: 18px;
}

.content {
  margin-top: 10px;
  font-size: 16px;
}

.right {
  flex: 1;
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.commentsSection {
  flex: 1;
  overflow-y: auto;
  padding-bottom: 60px; /* ÎåìÍ∏Ä ÏûëÏÑ±Ïπ∏ÏùÑ Ìï≠ÏÉÅ ÏïÑÎûòÏóê Í≥†Ï†ïÌïòÍ∏∞ ÏúÑÌïú Ïó¨Î∞± */
}

.commentList {
  margin-bottom: 20px;
}

.commentForm {
  position: sticky;
  bottom: 0;
  background-color: white;
  padding: 10px;
  display: flex;
  align-items: center; /* ÏöîÏÜåÎì§ÏùÑ ÏàòÌèâÏúºÎ°ú Ï†ïÎ†¨ */
  justify-content: space-between;
  border-top: 1px solid #ccc;
}

.commentInput {
  width: 80%;
  padding: 10px;
  height: 40px;  /* ÎÜíÏù¥Î•º Ï§ÑÏó¨ÏÑú Ìïú Ï§Ñ ÌÅ¨Í∏∞Î°ú */
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 14px; /* Í∏ÄÏûê ÌÅ¨Í∏∞ÎèÑ Ï§ÑÏó¨ÏÑú Ìïú Ï§ÑÏóê ÎßûÍ≤å */
  resize: none; /* ÌÅ¨Í∏∞ Ï°∞Ï†à Î∂àÍ∞Ä */
}

.commentSubmit {
  width: 15%;
  padding: 8px 16px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px; /* Î≤ÑÌäº Í∏ÄÏûê ÌÅ¨Í∏∞ Ï°∞Ï†ï */
  margin-bottom: 20px;
}

.commentSubmit:hover {
  background-color: #45a049;
}

.comment {
  margin-bottom: 20px;
}

.noComments {
  text-align: center;
  font-size: 16px;
  color: #999;
}

.imageSlider {
  position: relative;
  width: 100%;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 5px;
}

/* Ïù¥ÎØ∏ÏßÄ */
.postImage {
  width: 500px;
  height: 500px;
  object-fit: contain;
  border-radius: 5px;
}

.sliderControls {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  transform: translateY(-50%);
  width: 100%;
  pointer-events: none;
}

.prevButton,
.nextButton {
  display: flex !important;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0.3);
  color: white;
  border: none;
  padding: 5px;
  cursor: pointer;
  font-size: 16px;
  border-radius: 50%;
  pointer-events: auto;
  transition: background-color 0.2s;
}

.prevButton:hover,
.nextButton:hover {
  background-color: rgba(0, 0, 0, 0.5);
}

.prevButton {
  left: 5px;
}

.nextButton {
  right: 5px;
}

.loadMoreWrapper {
  display: flex;
  justify-content: center;
  width: 100%;
}

.loadMoreButton {
  padding: 10px 20px; /* Î≤ÑÌäº ÌÅ¨Í∏∞ Ï°∞Ï†ï */
  background-color: #007bff; /* ÌååÎûÄÏÉâ Î∞∞Í≤Ω */
  color: white;
  border: none;
  border-radius: 25px; /* Îë•Í∑º Î≤ÑÌäº Î™®ÏÑúÎ¶¨ */
  cursor: pointer;
  font-size: 16px; /* Í∏ÄÏûê ÌÅ¨Í∏∞ */
  transition: background-color 0.3s, transform 0.3s; /* Î≤ÑÌäº Î∞∞Í≤Ω ÏÉâÏÉÅÍ≥º Î≥ÄÌòï Ìö®Í≥º */
  box-shadow: 0 4px 10px rgba(0, 123, 255, 0.3); /* Í∑∏Î¶ºÏûê Ï∂îÍ∞Ä */
  margin-bottom: 10px;
}

.loadMoreButton:hover {
  background-color: #0056b3; /* ÎßàÏö∞Ïä§Î•º Ïò¨Î†∏ÏùÑ Îïå Î∞∞Í≤Ω ÏÉâÏÉÅ */
  transform: translateY(-3px); /* Î≤ÑÌäºÏùÑ ÏÇ¥Ïßù ÏúÑÎ°ú Ïò¨Î†§Ï£ºÎäî Ìö®Í≥º */
}

.loadMoreButton:active {
  background-color: #004085; /* Î≤ÑÌäº ÌÅ¥Î¶≠ Ïãú ÏÉâÏÉÅ */
  transform: translateY(1px); /* ÌÅ¥Î¶≠ÌñàÏùÑ Îïå Î≤ÑÌäºÏù¥ ÏÇ¥Ïßù ÎÇ¥Î†§Í∞ÄÎäî Ìö®Í≥º */
}

.commentContentAndLike {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.commentHeader {
  display: flex;
  align-items: center;
  margin-bottom: 5px; /* ÎãâÎÑ§ÏûÑÍ≥º ÎåìÍ∏Ä Í∞ÑÍ≤©ÏùÑ Ï§ÑÏù¥Í∏∞ ÏúÑÌï¥ margin Ï∂îÍ∞Ä */
}

.commentProfilePicture {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}

.commentNickname {
  font-weight: bold;
  margin-right: 10px; /* ÎåìÍ∏ÄÍ≥º ÎãâÎÑ§ÏûÑ ÏÇ¨Ïù¥Ïùò Í∞ÑÍ≤©ÏùÑ Ï§ÑÏó¨Ï§å */
}

.commentContent {
  font-size: 16px; /* ÎåìÍ∏Ä ÎÇ¥Ïö© Ìè∞Ìä∏ ÌÅ¨Í∏∞ */
  margin-top: 0; /* ÎåìÍ∏Ä ÎÇ¥Ïö© ÏÉÅÎã® Ïó¨Î∞± ÏóÜÏï†Í∏∞ */
  white-space: pre-line; /* Ï§Ñ Î∞îÍøàÏùÑ ÌóàÏö©Ìï©ÎãàÎã§ */
  word-wrap: break-word;
}

.likeButtonWrapper {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start; /* ÌïòÌä∏Î•º ÏúÑÎ°ú Ïò¨Î¶¨Í∏∞ ÏúÑÌï¥ flex-startÎ°ú ÏÑ§Ï†ï */
}

.likedButton, .unlikedButton {
  background-color: transparent;
  border: none;
  font-size: 18px; /* ÌïòÌä∏ ÌÅ¨Í∏∞ ÌÇ§Ïö∞Í∏∞ */
  cursor: pointer;
  margin-top: -20px; /* ÌïòÌä∏Î•º ÏúÑÎ°ú Ïò¨Î¶¨Í∏∞ ÏúÑÌïú ÏúÑÏπò Ï°∞Ï†ï */
}

.likedButton {
  color: red;
}

.unlikedButton {
  color: #ccc;
}

.likedButton:hover, .unlikedButton:hover {
  color: #007bff;
  background: #f0f0f0;
}
