.searchContainer {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
}

.searchForm {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.searchInput {
  width: 80%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.searchButton {
  padding: 10px 20px;
  margin-left: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 4px;
}

.searchButton:hover {
  background-color: #45a049;
}

.searchResults {
  margin-top: 20px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  padding: 5px;
}

.searchResultItem:hover {
  background-color: #f0f0f0;
}

.searchResultItem {
  display: flex;
  align-items: center;
  margin-bottom: 15px; /* Ìï≠Î™© ÏÇ¨Ïù¥ Í∞ÑÍ≤©ÏùÑ Îçî ÎÑìÌòÄÏÑú Í≤∞Í≥ºÎ•º Îçî ÎÑìÍ≤å ÌëúÏãú */
  padding: 10px; /* paddingÏùÑ ÎäòÎ†§ÏÑú Ìï≠Î™©Ïùò ÌÅ¨Í∏∞ ÌÇ§Ïö∞Í∏∞ */
  cursor: pointer;
  height: 60px; /* Ìï≠Î™© ÎÜíÏù¥Î•º ÎäòÎ†§ÏÑú Ï¢Ä Îçî Ïª§ÏßÄÍ≤å ÎßåÎì§Í∏∞ */
}

.profileInfo {
  display: flex;
  align-items: center;
  gap: 20px; /* ÏÇ¨ÏßÑÍ≥º Ïù¥Î¶Ñ Í∞ÑÍ≤©ÏùÑ Ï°∞Í∏à Îçî Î≤åÎ¶¨Í∏∞ */
}

.profilePicture {
  width: 50px; /* ÌîÑÎ°úÌïÑ ÏÇ¨ÏßÑ ÌÅ¨Í∏∞ Ï¶ùÍ∞Ä */
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
}

.nicknameSpan {
  margin-left: 20px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.nicknameSpan span {
  font-size: 20px; /* Í∏ÄÏî® ÌÅ¨Í∏∞Î•º Ï°∞Í∏à Îçî ÌÇ§ÏõåÏÑú Î≥¥Í∏∞ Ï¢ãÍ≤å */
  color: #333;
  font-weight: 700;
  letter-spacing: 0.5px;
  transition: color 0.3s ease;
}

.paginationContainer {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.loadMoreButton {
  background-color: #4CAF50;
  color: white;
  padding: 10px;
  border: none;
  cursor: pointer;
  width: 100%;
  text-align: center;
}

.loadMoreButton:hover {
  background-color: #45a049;
}
